
.Basket{
    width: 876px;
    border-radius: 6px;
    font-family: 'Open Sans', sans-serif;
    background-color: white;
    margin-bottom: 36px;
}

.BasketProduct{

}
.BasketDelivery{
    display: flex;
    justify-content:space-between;
    padding: 21px 44px 4px 29px;
}
.BasketDeliveryInfo{
    position: relative;
    padding: 3px 11px 0 0;
    border-bottom: 1px dashed black;
    position: relative;

}
.BasketDeliveryInfo:hover{
    color: #657EE2;
    cursor: pointer;
    border-bottom: 1px dashed #657EE2;
}
.BasketDeliveryInfo::after {
    content: "";
    position: absolute;
    top: 9px;
    width: 12px;
    height: 12px;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center center;
    background-image: url('./../img/Vector2.png');

}
.BasketDeliveryInfo:hover::after {
    content: "";
    position: absolute;
    background-image: url('./../img/Vector3.png');
}

.BasketDeliveryInfo:hover img{
    opacity: 0.5;
}
.BasketDeliveryCity{
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 27px;
}
.BasketDeliveryDate{
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    padding-right: 10px;
}
.BasketDeliveryLogo{
    padding-right: 9px;
    width: 39px;
    height: 11px;
}



.BasketDeliveryVector{
}
.MeinInfo{
    width: 268px;
}
.Delivery{
    height: 324px;
    background-color: #ffffff;
    border-radius: 6px;
    margin-bottom: 26px;
}
.FullPrice{
    background-color: #ffffff;
    border-radius: 6px;
    height: 511px;
}
.DeliveryType{
    padding: 22px 0 0 30px;
}
.DeliveryTypeData{
    padding: 14px 0 0 31px;
}
.DeliveryTypeItem{
    margin-bottom: 7px;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
    color: #848DAF;
}

@media screen and (max-width: 1280px) {
    .Basket{
        width: 686px;
    }
    .BasketDeliveryInfo{
        padding: 3px 2px 0 0;

    }
}
