.Product{
    border-top: 1px solid rgba(173,182,216, 0.3);
    max-width: 806px;
    height: 109px;
    display: flex;
    background-color: #ffffff;
    margin: 0 40px 0 30px;
    padding: 16px 0 0px 0;
}
.Product:nth-child(1){
    border: none;
}

.ProductImg{
    padding: 8px 17px 0 9px;
     width: 100px;
     height: 75px;
}
.ProductImg img{
    width: 100px;
    height: 75px;
}
.ProductName{
    width: 358px;
    display: flex;
    flex-direction: column;
}
.ProductNameItem{
    padding: 3px 0 0 0;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 19px;
    color: #00145C;

}
.ProductNameItem:hover{
    color: #657EE2;
    cursor: pointer;
}
.ProductNameLoca{
    flex: 1 1 auto;
    display: flex;
    align-items: flex-end;
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 16px;
    padding-bottom: 22px;
}
.CheckboxMein {
    position: absolute;
    z-index: -1;
    opacity: 0;
}
.CheckboxMein+label {
    margin-top: 6px;
    display: inline-flex;
    align-items: center;
    user-select: none;
}
.CheckboxMein+label:hover {
    color: #243DA1;
    cursor: pointer;
}
.CheckboxMein+label::before {
    content: '';
    display: inline-block;
    width: 20px;
    height: 20px;
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid #ADB6D8;
    border-radius: 3px;
    margin-right: 11px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
    background-color: white;
}
.CheckboxMein:not(:disabled):not(:checked)+label:hover::before {
    border-color:#243DA1;
}
.CheckboxMein:checked+label::before {
    background-color:#243DA1;
    background-image: url('../../img/Vector4.png');
}
.CheckboxMein:checked+label:hover::before {
    background-color: #657EE2;

}
.cCheckboxMein:disabled+label::before {
    background-color: #e9ecef;
}

.ProductCount{
    margin: -1px 0 0 45px;
    width: 100px;
}
.ProductMaxCount{
    margin: 4px 0 0 8px;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
    color: #6C6C6C;
    opacity: 0.6;
}
.ProductButton button{
    width: 27px;
    height: 27px;
    background: #EBF0F3;
    border-radius: 50%;
    border: 1px solid #EBF0F3;
    color: #202020;
    cursor: pointer;
}
.ProductButton {
    display: flex;
}
.ProductButtonPlus:before {
    content: '';
    width: 10.5px;
    height: 1px;
    border-top: 1px solid #B4B4B4;
    display: block;
    position: absolute;
    margin-left: 1px;

}
.ProductButtonPlus:after {
    content: '';
    width: 1px;
    height:10.5px;
    border-right: 1px solid #B4B4B4;
    display: block;
    position: absolute;
    margin-top: -5px;
    margin-left: 5px;
}
.ProductButtonPlus:hover{
    border: 1px solid #657EE2;
}
.ProductButtonPlus:hover:after{

    border-right: 1px solid #657EE2;
}
.ProductButtonPlus:hover:before{
    border-top: 1px solid #657EE2;
}
.ProductButtonMinus:before {
    content: '';
    width: 10.5px;
    height: 1px;
    border-top: 1px solid #B4B4B4;
    display: block;
    position: absolute;
    margin-left: 1px;

}
.ProductButtonMinus:hover{
    border: 1px solid #657EE2;
}

.ProductButtonMinus:hover:before{
    border-top: 1px solid #657EE2;
}


.ProductButtonCount{
    text-align: center;
    width: 26px;

}
.ProductPrice{
    text-align: right;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 22px;
    color: #000000;
    width: 104px;
}
.ProductCountPrice{
    display: flex;

}
.ProductRight{


}
.ProductArti{
    width: 109px;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
    color: #6C6C6C;
    opacity: 0.6;
    margin: 24px 0 0 161px;
}
.ProductDelete{
    margin: 5px 0 0 0;

}
.ProductDeleteClose{
    position: absolute;
    width: 32px;
    height: 32px;
    opacity: 0.3;
}

.ProductDeleteClose:hover {
    opacity: 0.8;
    cursor: pointer;
}
.ProductDeleteClose:hover:after, .ProductDeleteClose:hover:before {
    background:#ED3135;

}
.ProductDeleteClose:before, .ProductDeleteClose:after {
    position: absolute;
    top: -1px;
    left: 12px;
    content: ' ';
    height: 15px;
    width: 2px;
    background: #00145c;

}
.ProductDeleteClose:before {
    transform: rotate(45deg);
}
.ProductDeleteClose:after {
    transform: rotate(-45deg);
}
@media screen and (max-width: 1280px) {
    .Product {
        margin: 0px 0 0 26px;
    }
    .ProductPrice{
        width: 62px;
    }
    .ProductArti{
        margin: 24px 0 0 123px;
        width: auto;
    }
    .ProductCount{
        margin: 0 2px 0 46px;
    }
    .ProductName{
        width: 228px;
    }
    .ProductDelete{
         margin: 5px 0 0 -9px;
     }

}